import { Avatar, Button, Card, Col, Divider, PageHeader, Row } from "antd";
import { MY_PROFILE_LABELS, PROFILE_FORM_LABELS } from "constants/MyProfileConstant";
import {
  SyncOutlined,
  CalendarTwoTone,
  MailTwoTone,
  PhoneTwoTone,
  GlobalOutlined,
  UserOutlined,
  PlusCircleOutlined,
} from "@ant-design/icons";
import Flex from "components/shared-components/Flex";
import moment from "moment";
import useIntlMessage from "hooks/useIntlMessage";
import UpdateProfile from "./ProfileUpdate";
import { useEffect, useState } from "react";
import employeeService from "services/EmployeeService";
import { useSelector } from "react-redux";
import { useDispatch } from "react-redux";
import { showLoader } from "../../../redux/actions/Loader";
import Utils from "utils";
import DownloadDocumentButton from "components/util-components/Document/DownloadDocumentButton";
import UploadDocumentButton from "components/util-components/Document/UploadDocument";
import uploadDataMethods from "utils/UploadDataFunctions";
import { DATE_FORMAT_MMM_DD_YYYY, DATE_FORMAT_YYYY_MM_DD, SCREEN_ITEMS_TYPES } from "constants/CommonConstants";
import { FORM_FIELDS } from "constants/ChangeAllocationStatusConstant";
import ResumeComponent from "components/shared-components/ResumeComponent";
import employeeResumeService from "services/EmployeeResumeService";
import UploadData from "components/UploadData";
import { Typography } from "antd";

const MyProfile = () => {
  const dispatch = useDispatch();
  const [show, setShow] = useState(false);
  const [profileData, setProfileData] = useState([]);
  const [profileUpdateData, setProfileUpdateData] = useState(null);
  const employeeId = useSelector(state => state.myTeams.loggedinId);
  const userRole = useSelector(state => state?.myTeams?.userInfo?.role);
  const [isUploaded, setIsUploaded] = useState(false);
  const [showUploadResumeModal, setShowUploadResumeModal] = useState(false);
  const [fetchDocuments, setFetchDocuments] = useState(true);
  const { Text } = Typography;

  const getMessage = useIntlMessage();
  useEffect(() => {
    if (employeeId) {
      getEmployeeById();
    }
  }, [dispatch, employeeId]);

  const handleClick = () => {
    setProfileUpdateData(profileData);
    setShow(true);
  };

  const getEmployeeById = async () => {
    try {
      dispatch(showLoader(true));
      const response = await employeeService.getSingleEmployeeById(employeeId);
      setProfileData(response);
    } catch (e) {
      console.error("Error in getting the profile information", e);
    } finally {
      dispatch(showLoader(false));
    }
  };

  const handleRefresh = () => {
    getEmployeeById();
  };
  const MaskedEmail = profileData => {
    let maskedid = "";
    let myemailId = profileData?.personalEmailId;
    let index = myemailId?.lastIndexOf("@");
    let prefix = myemailId?.substring(0, index);
    let postfix = myemailId?.substring(index);
    let mask = prefix
      ?.split("")
      .map(function (value, i) {
        if (i === 0 || i === index - 1) {
          return value;
        } else {
          return "*";
        }
      })
      .join("");
    maskedid = mask + postfix;
    return maskedid;
  };

  const handleDownloadDocument = async () => {
    await uploadDataMethods.downloadEmployeeResume(employeeId);
  };

  const handleFileUpload = async info => {
    setIsUploaded(true);
    await uploadDataMethods.uploadEmployeeResume(info, employeeId);
    setIsUploaded(false);
  };

  return (
    <>
      <Row align="middle" gutter={[20, 20]} className="my-5">
        <Col xs={24} md={24} xl={6}>
          <PageHeader
            className="p-2 pl-2"
            title={getMessage("component.myprofile.title")}
            subTitle={
              <Button className="ml-2" onClick={handleRefresh}>
                <SyncOutlined shape="circle" />
              </Button>
            }
          />
        </Col>
        {userRole !== "Report_Viewer" && (
          <Col className="mb-2 text-right" xs={24} md={24} xl={18}>
            {/* <DownloadDocumentButton
            disable={isUploaded}
            label={getMessage(PROFILE_FORM_LABELS.DOWNLOAD_RESUME_TEXT.text)}
            classNames={"mr-2"}
            onClick={handleDownloadDocument}
          /> */}
            {/* <UploadDocumentButton
            employeeId={employeeId}
            onChange={handleFileUpload}
            accept={".doc,.docx,.pdf"}
            label={getMessage(PROFILE_FORM_LABELS.UPLOAD_RESUME_TEXT.text)}
          /> */}
            <Button type="primary" className="mr-3" onClick={() => setShowUploadResumeModal(!showUploadResumeModal)}>
              <PlusCircleOutlined className="mr-2" />
              {getMessage("common.upload.resume")}
            </Button>
            <Button type="primary" onClick={handleClick}>
              {getMessage(PROFILE_FORM_LABELS.UPDATE_PROFILE_TEXT.text)}
            </Button>
          </Col>
        )}
      </Row>
      <Card>
        <Row justify="center" className="my-4">
          <Col sm={24} md={24}>
            <div className="d-md-flex">
              <div
                className="rounded p-2 bg-white shadow-sm mx-auto radius"
                style={{ marginTop: "-4.5rem", maxHeight: "218px", maxWidth: "215px" }}
              >
                <Avatar shape="square" size={200} icon={<UserOutlined />} />
              </div>
              <div className="ml-md-4 w-100">
                <Flex alignItems="center" mobileFlex={false} className="mb-3 text-md-left text-center">
                  <h2 className="mb-0 mt-md-0 mt-2">{profileData?.employeeName}</h2>
                </Flex>
                <Row className="mb-1">
                  <Col xs={12} sm={12} md={12} lg={6} className="mb-1">
                    <span className="ml-2 font-weight-semibold">
                      <MailTwoTone className="text-primary font-size-md mr-2" />{" "}
                      {getMessage(MY_PROFILE_LABELS.PERSONAL_EMAIL_ID_LABEL.text)}:
                    </span>
                  </Col>
                  <Col xs={12} sm={12} md={12} lg={6}>
                    <span className="profile-text-color">{MaskedEmail(profileData)}</span>
                  </Col>
                  <Col xs={12} sm={12} md={12} lg={6} className="mb-1">
                    <span className="ml-2 font-weight-semibold">
                      <PhoneTwoTone className="text-primary font-size-md mr-2" />{" "}
                      {getMessage(PROFILE_FORM_LABELS.MOBILE_NO_LABEL.text)}:
                    </span>
                  </Col>
                  <Col xs={12} sm={12} md={12} lg={6}>
                    <span className="profile-text-color">
                      {profileData?.mobileNumber ? Utils.maskCharacters(profileData?.mobileNumber) : "-"}
                    </span>
                  </Col>
                </Row>
                <Row className="mb-2">
                  <Col xs={12} sm={12} md={12} lg={6}>
                    <span className="ml-2 font-weight-semibold">
                      <CalendarTwoTone className="text-primary font-size-md mr-2" />{" "}
                      {getMessage(PROFILE_FORM_LABELS.DATE_OF_BIRTH_LABEL.text)}:
                    </span>
                  </Col>
                  <Col xs={12} sm={12} md={12} lg={6}>
                    <span className="profile-text-color">
                      {`${moment(profileData?.dateOfBirth).format(DATE_FORMAT_YYYY_MM_DD)?.slice(0, 6)}${"*".repeat(
                        4,
                      )}`}
                    </span>
                  </Col>
                  <Col xs={12} sm={12} md={12} lg={6}>
                    <span className="ml-2 font-weight-semibold">
                      <GlobalOutlined className="text-primary font-size-md mr-2" />{" "}
                      {getMessage(MY_PROFILE_LABELS.COUNTRY_LABEL.text)}:
                    </span>
                  </Col>
                  <Col xs={12} sm={12} md={12} lg={6}>
                    <span className="profile-text-color">{profileData?.country ? profileData?.country : "-"}</span>
                  </Col>
                </Row>
              </div>
            </div>
          </Col>
        </Row>
        <Divider />
        <Row justify="center">
          <Col sm={24} md={24}>
            <Row className="mb-1">
              <Col xs={12} sm={12} md={12} lg={4} xl={4} className="mb-1">
                <span className="ml-2 font-weight-semibold">{getMessage(MY_PROFILE_LABELS.EMAIL_ID_LABEL.text)}:</span>
              </Col>
              <Col xs={12} sm={12} md={12} lg={4} xl={4}>
                <span className="profile-text-color">{profileData?.emailId}</span>
              </Col>
              <Col xs={12} sm={12} md={12} lg={4} xl={4} className="mb-1">
                <span className="ml-2 font-weight-semibold">
                  {getMessage(MY_PROFILE_LABELS.DESIGNATION_COLUMN.text)}:
                </span>
              </Col>
              <Col xs={12} sm={12} md={12} lg={4} xl={4}>
                <span className="profile-text-color">{profileData?.designation}</span>
              </Col>
              <Col xs={12} sm={12} md={12} lg={4} xl={4}>
                <span className="ml-2 font-weight-semibold">
                  {getMessage(MY_PROFILE_LABELS.EMPLOYEE_ID_LABEL.text)}:
                </span>
              </Col>
              <Col xs={12} sm={12} md={12} lg={4} xl={4}>
                <span className="profile-text-color">{profileData?.employeeId}</span>
              </Col>
            </Row>
            <Row className="mb-1">
              <Col xs={12} sm={12} md={12} lg={4} xl={4} className="mb-1">
                <span className="ml-2 font-weight-semibold">
                  {getMessage(PROFILE_FORM_LABELS.FUNCTIONAL_ORG_LABEL.text)}:
                </span>
              </Col>
              <Col xs={12} sm={12} md={12} lg={4} xl={4}>
                <span className="profile-text-color">{profileData?.functionalGroup}</span>
              </Col>
              <Col xs={12} sm={12} md={12} lg={4} xl={4} className="mb-1">
                <span className="ml-2 font-weight-semibold">{getMessage(MY_PROFILE_LABELS.HRMS_ID_LABEL.text)}:</span>
              </Col>
              <Col xs={12} sm={12} md={12} lg={4} xl={4}>
                <span className="profile-text-color">{profileData?.hrmsId}</span>
              </Col>
              <Col xs={12} sm={12} md={12} lg={4} xl={4}>
                <Text className="ml-2 font-weight-semibold">
                  {getMessage(PROFILE_FORM_LABELS.TECHNOLOGIES_KNOWN.text)}:
                </Text>
              </Col>
              <Col xs={12} sm={12} md={12} lg={4} xl={4}>
                <Text className="profile-text-color">{profileData?.technologyKnown}</Text>
              </Col>
            </Row>
            <Row className="mb-1">
              <Col xs={12} sm={12} md={12} lg={4} className="mb-1">
                <span className="ml-2 font-weight-semibold">
                  {getMessage(MY_PROFILE_LABELS.EMPLOYEMENT_TYPE_LABEL.text)}:
                </span>
              </Col>
              <Col xs={12} sm={12} md={12} lg={4}>
                <span className="profile-text-color">{profileData?.employmentType}</span>
              </Col>
              <Col xs={12} sm={12} md={12} lg={4}>
                <span className="ml-2 font-weight-semibold">{getMessage(MY_PROFILE_LABELS.ROLE_COLUMN.text)}:</span>
              </Col>
              <Col xs={12} sm={12} md={12} lg={4}>
                <span className="profile-text-color">{profileData?.role}</span>
              </Col>
            </Row>
            <Row className="mb-1">
              <Col xs={12} sm={12} md={12} lg={4} className="mb-1">
                <span className="ml-2 font-weight-semibold">{getMessage(MY_PROFILE_LABELS.EMP_STATUS.label)}:</span>
              </Col>
              <Col xs={12} sm={12} md={12} lg={4}>
                <span className="profile-text-color">{profileData?.empStatus}</span>
              </Col>
              <Col xs={12} sm={12} md={12} lg={4} className="mb-1">
                <span className="ml-2 font-weight-semibold">
                  {getMessage(PROFILE_FORM_LABELS.ALT_MOBILE_NUMBER_LABEL.text)}:
                </span>
              </Col>
              <Col xs={12} sm={12} md={12} lg={4}>
                <span className="profile-text-color">
                  {profileData?.alternateMobileNumber ? Utils.maskCharacters(profileData?.alternateMobileNumber) : "-"}
                </span>
              </Col>
              <Col xs={12} sm={12} md={12} lg={4}>
                <span className="ml-2 font-weight-semibold">
                  {getMessage(PROFILE_FORM_LABELS.PASSPORT_EXPIRY_DATE.text)}:
                </span>
              </Col>
              <Col xs={12} sm={12} md={12} lg={4}>
                <span className="profile-text-color">
                  {profileData?.passportExpiryDate
                    ? `${profileData?.passportExpiryDate?.slice(0, 7)}${"*".repeat(4)}`
                    : "N/A"}
                </span>
              </Col>
            </Row>
            <Row className="mb-1">
              <Col xs={12} sm={12} md={12} lg={4} className="mb-1">
                <span className="ml-2 font-weight-semibold">
                  {getMessage(MY_PROFILE_LABELS.SHIFT_TIMING_LABEL.text)}:
                </span>
              </Col>
              <Col xs={12} sm={12} md={12} lg={4}>
                <span className="profile-text-color">{profileData?.shift}</span>
              </Col>
              <Col xs={12} sm={12} md={12} lg={4} className="mb-1">
                <span className="ml-2 font-weight-semibold">{getMessage(MY_PROFILE_LABELS.LOCATION_LABEL.text)}:</span>
              </Col>
              <Col xs={12} sm={12} md={12} lg={4}>
                <span className="profile-text-color">{profileData?.empLocation}</span>
              </Col>
              {/* <Col xs={12} sm={12} md={12} lg={4}>
                <span className="ml-2 font-weight-semibold">
                  {getMessage(PROFILE_FORM_LABELS.LAST_RESUME_UPLOAD_LABEL.text)}:
                </span>
              </Col>
              <Col xs={12} sm={12} md={12} lg={4}>
                <span className="profile-text-color">N/A</span>
              </Col> */}
            </Row>
            <Row className="mb-1">
              <Col xs={12} sm={12} md={12} lg={4} className="mb-1">
                <span className="ml-2 font-weight-semibold">{getMessage(MY_PROFILE_LABELS.DATE_OF_JOINING.text)}:</span>
              </Col>
              <Col xs={12} sm={12} md={12} lg={4}>
                <span className="profile-text-color">
                  {moment(profileData?.dateOfJoining).format(DATE_FORMAT_YYYY_MM_DD)}
                </span>
              </Col>
              <Col xs={12} sm={12} md={12} lg={4} className="mb-1">
                <span className="ml-2 font-weight-semibold">
                  {getMessage(MY_PROFILE_LABELS.EXPERIENCE_LABEL.text)}:
                </span>
              </Col>
              <Col xs={12} sm={12} md={12} lg={4}>
                <span className="profile-text-color">{profileData?.totalExperience}</span>
              </Col>
              <Col xs={12} sm={12} md={12} lg={4}>
                <span className="ml-2 font-weight-semibold">
                  {getMessage(PROFILE_FORM_LABELS.PRIMARY_SKILL_LABEL.text)}:
                </span>
              </Col>
              <Col xs={12} sm={12} md={12} lg={4}>
                <span className="profile-text-color">{profileData?.baseTechnology?.replace(/,/g, ", ")}</span>
              </Col>
              <Col xs={12} sm={12} md={12} lg={4}>
                <span className="ml-2 font-weight-semibold">{getMessage(FORM_FIELDS?.ALLOCATION_LEVEL?.label)}:</span>
              </Col>
              <Col xs={12} sm={12} md={12} lg={4}>
                <span className="profile-text-color">{profileData?.assignedLevel}</span>
              </Col>
              <Col xs={12} sm={12} md={12} lg={4}>
                <span className="ml-2 font-weight-semibold">
                  {getMessage(PROFILE_FORM_LABELS.LAST_RESUME_UPLOAD_DATE_LABEL.text)}:
                </span>
              </Col>
              <Col xs={12} sm={12} md={12} lg={4}>
                <span className="profile-text-color">
                  {moment(profileData?.lastResumeUploadDate).format(DATE_FORMAT_YYYY_MM_DD)}
                </span>
              </Col>
            </Row>
          </Col>
        </Row>
        {userRole !== "Report_Viewer" && (
          <ResumeComponent
            showTitle="true"
            setFetchDocuments={setFetchDocuments}
            fetchDocuments={fetchDocuments}
            empId={profileData?.employeeId}
          />
        )}
      </Card>
      {show && (
        <UpdateProfile
          setIsVisible={show}
          profileUpdateData={profileUpdateData}
          getEmployeeById={getEmployeeById}
          onCancel={() => {
            setShow(false);
          }}
        />
      )}

      {showUploadResumeModal && (
        <UploadData
          visible={showUploadResumeModal}
          onCancel={() => setShowUploadResumeModal(!showUploadResumeModal)}
          screen={SCREEN_ITEMS_TYPES.uploadResume}
          empId={employeeId}
          setFetchDocuments={setFetchDocuments}
        />
      )}
    </>
  );
};

export default MyProfile;
